.weather_block
    display: flex
    flex-direction: column
    justify-content: space-between
    position: relative
    height: 385px
    .weather_container
        border-right: 1px solid #000000
        height: 100%
        .weather_now
            display: none
            .weather_temp 
                font-size: 70px
                margin: 20px 20px 0 20px
            .weather_cloudly
                display: flex
                .weather_cloudly-icon  
                    margin: auto
                    height: 175px
                    opacity: 1
            .weather_city
                display: flex
                justify-content: space-between
                align-items: center
                margin: 17px 12px
                .weather_like-button
                    position: relative
                    width: 20px
                    height: 20px
                .weather_like-button::before,
                .weather_like-button::after
                    position: absolute
                    content: ""
                    width: 16px
                    height: 27px
                    top: -5px
                    background-color: #A9A9A9
                    border-radius: 30px 30px 0 0
                    transform: rotate(-45deg)
                    transform-origin: 0 100%
                .weather_like-button::after
                    right: 20px
                    transform-origin: 100% 100%
                    transform: rotate(46deg)
                .weather_like-button::before
                    right: 5px
                .liked::before,
                .liked::after
                    position: absolute
                    content: ""
                    width: 16px
                    height: 27px
                    top: -5px
                    background-color: #FF6347
                    border-radius: 30px 30px 0 0
                    transform: rotate(-45deg)
                    transform-origin: 0 100%
                .liked::after
                    right: 20px
                    transform-origin: 100% 100%
                    transform: rotate(46deg)
                .liked::before
                    right: 5px
        .weather_now:target
            display: contents
        .weather_details
            display: none
            height: 100%
            .weather_city-name
                margin: 36px 17px
            .weather_city-info
                margin: 0px 17px
                .info
                    font-size: 24px
                    padding: 6px 0px
                    span
                        font-size: 24px
        .weather_details:target
            display: contents
        .weather_forecast
            display: none
            .weather_city-name
                margin: 36px 17px 0 17px
            .weather_forecast-cards 
                .forecast_card 
                    border: 1px solid #000000
                    margin: 9px 17px
                    padding: 6px 10px
                    .forecast_card-date
                        display: flex
                        justify-content: space-between
                        margin: 0 0 38px 0
                        .date
                        .time
                    .forecast_card-weather
                        display: flex
                        justify-content: space-between
                        align-items: center
                        .forecast_card-temp 
                            .temp 
                            .weather_feels-like 
                        .forecast_card-icon
        .weather_forecast:target
            display: contents 
    .weather_tabs
        display: grid
        grid-template-columns: 1fr 1fr 1fr
        .now,
        .details, 
        .forecast
            border: 1px solid #000000
            border-right: none
            border-bottom: none
            background-color: #ffffff
            padding: 10px 15px
            text-align: center
        .now
            border-left: none
        .forecast
            border-right: 1px solid #000
        .now:hover,
        .details:hover, 
        .forecast:hover
            background-color: black
            color: white 
        .active-tab
            background-color: black
            color: white 
 